import DashView from './components/Dash.vue'
import LoginView from './components/Login.vue'
import NotFoundView from './components/404.vue'
// Import Views - Dash
import DashboardView from './components/views/Dashboard.vue'
import ChatBotView from './components/views/ChatBot.vue'
import TasksView from './components/views/Tasks.vue'
import SettingView from './components/views/Setting.vue'
import AccessView from './components/views/Access.vue'
import ServerView from './components/views/Server.vue'
import ReposView from './components/views/Repos.vue'
import NotificationsView from './components/views/Notifications.vue'
import EntitiesView from './components/views/Entities.vue'
import ProfilesView from './components/views/Profiles.vue'
import UsersView from './components/views/Users.vue'
import DevicesView from './components/views/Devices.vue'

// Routes
const routes = [
  {
    path: '/login',
    component: LoginView
  },
  {
    path: '/',
    component: DashView,
    children: [
      {
        path: 'dashboard',
        alias: '',
        component: DashboardView,
        name: 'Dashboard',
        meta: {description: 'Overview of environment'}
      }, {
        path: 'devices',
        component: DevicesView,
        name: 'Devices',
        meta: { description: 'Devices for the Telemetry Platform' }
      }, {
        path: 'notifications',
        component: NotificationsView,
        name: 'Notifications',
        meta: { description: 'Notifications for the Telemetry Platform' }
      }, {
        path: 'entities',
        component: EntitiesView,
        name: 'Entities',
        meta: { description: 'Entities for the Telemetry Platform' }
      }, {
        path: 'profiles',
        component: ProfilesView,
        name: 'Profiles',
        meta: { description: 'Profiles for the Telemetry Platform' }
      }, {
        path: 'users',
        component: UsersView,
        name: 'Users',
        meta: { description: 'Users for the Telemetry Platform' }
      }, {
        path: 'chatbot',
        component: ChatBotView,
        name: 'ChatBot',
        meta: {description: 'ChatBot for the Telemetry Platform'}
      }, {
        path: 'tasks',
        component: TasksView,
        name: 'Tasks',
        meta: {description: 'Tasks page in the form of a timeline'}
      }, {
        path: 'setting',
        component: SettingView,
        name: 'Settings',
        meta: {description: 'User settings page'}
      }, {
        path: 'access',
        component: AccessView,
        name: 'Access',
        meta: {description: 'Example of using maps'}
      }, {
        path: 'server',
        component: ServerView,
        name: 'Servers',
        meta: {description: 'List of our servers', requiresAuth: true}
      }, {
        path: 'repos',
        component: ReposView,
        name: 'Repository',
        meta: {description: 'List of popular javascript repos'}
      }
    ],
    meta: {requiresAuth: false}
  }, {
    // not found handler
    path: '*',
    component: NotFoundView
  }
]
export default routes



